.footer .wrap {
  background: #4bfabf url(images/graphics/bg-footer.png) no-repeat center bottom;
  padding: 12px 10px 90px 10px;
  border-radius: 10px 10px 0 0;
}
.footer .grid {
  column-gap: 10px;
  row-gap: 10px;
  margin-bottom: 8px;
}
.footer .social-link {
  border-radius: 8px;
  border: 1px solid rgba(0, 125, 227, 0.5);
  color: #007DE3;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  gap: 4px;
  font-weight: 500;
  font-size: 14px;
}
.footer .face {
  grid-column: 2 span;
}
.footer .tiktok {
  border: 1px solid rgba(42, 42, 42, 0.5);
  color: rgb(42, 42, 42);
}
.footer .youtube {
  border: 1px solid rgba(255, 44, 43, 0.5);
  color: rgb(255, 44, 43);
}
.footer .g-number {
  text-align: center;
  font-size: 12px;
  font-weight: 300;
  color: #034C31;
  column-gap: 8px;
  row-gap: 8px;
}
.footer .g-number div {
  position: relative;
}
.footer .g-number div::before {
  content: "";
  position: absolute;
  width: 1px;
  right: -6px;
  top: 9px;
  height: 20px;
  background: linear-gradient(90deg, rgba(73, 245, 185, 0) 0%, rgba(6, 78, 51, 0.2) 50.48%, rgba(73, 245, 185, 0) 100%);
}
.footer .g-number div:last-child::before {
  display: none;
}
.footer .g-number .number {
  font-size: 14px;
  font-weight: 500;
}
.footer .g-number .number span {
  color: #FF5E57;
}
.footer .g-number p {
  margin-bottom: 0;
}
.footer .option-bottom {
  justify-content: center;
  column-gap: 12px;
  row-gap: 12px;
  position: relative;
  padding-top: 15px;
  margin-top: 15px;
}
.footer .option-bottom::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, rgba(73, 245, 185, 0) 0%, rgba(6, 78, 51, 0.2) 50.48%, rgba(73, 245, 185, 0) 100%);
}
.footer .option-bottom .itemt .modal-content {
  position: absolute;
  left: 0;
  bottom: 40px;
  border: 0;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16);
  transition: all 0.25s ease-in-out;
  opacity: 0;
  visibility: hidden;
}
.footer .option-bottom .itemt .modal-content .modal-header {
  padding: 10px 16px;
}
.footer .option-bottom .itemt .modal-content .modal-body {
  font-size: 13px;
}
.footer .option-bottom .itemt > .ic {
  border-radius: 4px;
  width: 30px;
  height: 30px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #999;
  cursor: pointer;
}
.footer .option-bottom .itemt > .ic svg {
  width: 22px;
  height: 22px;
}
.footer .option-bottom .itemt > .ic.active {
  background: #D0EAE2;
  color: var(--colormain);
}
.footer .option-bottom .itemt > .ic.active + .modal-content {
  opacity: 1;
  visibility: visible;
}
.footer .option-bottom .itemt:hover > .ic {
  background: #D0EAE2;
  color: var(--colormain);
}
.footer.footer-mobile {
  display: none;
}
.footer.footer-mobile .wrap {
  border-radius: 0;
  background: #4bfabf url(images/graphics/m_bg-footer.png) no-repeat center bottom;
  padding: 12px 10px 130px 10px;
  background-size: contain;
}
.footer.footer-mobile .grid__4 {
  grid-template-columns: repeat(4, 1fr);
}
.footer.footer-mobile .g-number {
  max-width: 300px;
  margin: 0 auto 15px;
}
@media (max-width: 600px) {
  .footer.footer-mobile {
    display: block;
    padding-bottom: 30px;
    background: #fff;
  }
  .footer .option-bottom {
    column-gap: 15px;
    row-gap: 15px;
  }
  .footer .option-bottom .itemt > .ic {
    width: 48px;
    height: 48px;
  }
  .footer .option-bottom .itemt > .ic img, .footer .option-bottom .itemt > .ic svg {
    width: 24px;
    height: 24px;
  }
}